import './application.css'
import Draggable from 'react-draggable';
import { useState, useEffect } from 'react';
//var loot = require('../apps/loot/index.html')
//import loot from '../apps/loot/index.html'
const Application = (props) => {
    const [active, setActive] = useState(props.status);
    useEffect(() => {
        setActive(props.status);
    }, [props]);
    return (
        <div className="application">
            {active &&

                <Draggable handle=".banner">
                    <div className="window">
                        <div className="banner">
                            <div><p className="title">About.exe</p></div>
                            <button onClick={() => setActive(false)}>
                                <b>X</b>
                            </button>
                        </div>
                        <div className="app">
                            <div className="content">
                                <h1>Hi! I'm Devendra!</h1>
                                <hr />
                                <p>I like creating websites in React.JS.</p>

                            </div>

                        </div>
                    </div>
                </Draggable>
            }
        </div>
    );
}

export default Application;