import { useState, useEffect } from 'react';
import './clock.css'

const Clock = () => {
    const [date, setDate] = useState(new Date());
    //sets state of date to JS DAte object
    useEffect(() => {
        setInterval(() => tick(), 1000);
    });
    //side effect of calling the tick function every second

    const tick = () => {
        setDate(new Date());
    }

    //tik function setting date on every second

    return (
        <div className="clock">
            <h2>{date.toLocaleTimeString()}.</h2>
        </div>
    );
    //return statement
}

export default Clock;
