import "./exe.css";
import Application from "../application/application";

const { useState } = require("react");

const Exe = (props) => {
    const [active, setActive] = useState(false);
    return (
        <div>
            <div class="exe" onClick={() => setActive(!active)}>
                <img class="icons" src={props.image}/>
                <p>{props.name}</p>
            </div>
            <Application status={active}/>
        </div>
    );
}

export default Exe;

